
import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import constantes from '../constantes';
import {useAutenticacion} from './auth';

export function Signin (props){

    const [data,setData] = useState({}) 

    const {logear} = useAutenticacion();
    const history = useHistory();


    function handleOnchange({target:{name,value}}){
        setData({...data,[name]:value})
    }

    async function submit(event){
       event.preventDefault();
       const response = await axios.post(constantes.BACKEND_URL + "/api/signin",data);
       console.log(response);
        
        if(response.status != 200){
            console.error("error");
        }else{
            logear();
            history.push('/products');
        }

        
    }

    return(
        <div className="signin">
            <form className="login" onSubmit={submit} >
            <div className="login-header">
                <h1>Login</h1>
            </div>
            <div className="login-form">
                <h3>Email:</h3>
                <input type="email" placeholder="email" name="email" id="email" onChange={handleOnchange}/><br/>
                <h3>Password :</h3>
                <input type="password" placeholder="Password" name="password" id="password" onChange={handleOnchange}/>
                <br/>
                <button type="submit" className="login-button" onSubmit={submit}>Login</button>
            </div>
        </form>
    </div>
    );
}

export default Signin;