import React from 'react';
import { useState } from 'react';
import { useHistory} from 'react-router-dom';
import axios from 'axios';
import constantes from '../constantes';
import {useAutenticacion} from './auth';


export function Signup (props){

   const [username,setUsername] = useState("");
   const [useremail,setUseremail] = useState("");
   const [userpassword,setUserpassword] = useState("");

   const {logear} = useAutenticacion();
   const history = useHistory();

   async function register(event){
    event.preventDefault();
    const response = await axios.post(constantes.BACKEND_URL + "/api/signup",{
        name:username,
        email:useremail,
        password:userpassword
    });
    console.log(response);
     
     if(response.status != 200){
         console.error("error");
     }else{
         console.log("Hola");
         logear();
         history.push('/products');
     }
 }

   return(
    <div className="signup">
    <form className="login" onSubmit={register} >
    <div className="login-header">
        <h1>Login</h1>
    </div>
    <div className="login-form">
        <h3>Username:</h3>
        <input type="text" placeholder="Username" name="name" id="name" onChange={(e) => {setUsername(e.target.value);}}/><br/>
        <h3>Email:</h3>
        <input type="email" placeholder="Email" name="email" id="email" onChange={(e) => {setUseremail(e.target.value);}}/><br/>
        <h3>Password :</h3>
        <input type="password" placeholder="Password" name="password" id="password" onChange={(e) => {setUserpassword(e.target.value);}}/>
        <br/>
        <button type="submit" className="login-button" onSubmit={register}>Register</button>
    </div>
</form>
</div>
    );
}

export default Signup;