import React from 'react';
import {useAutenticacion} from './auth';
import {Redirect,Route} from 'react-router-dom';

export function RutaPrivada({children,...rest}) {
    const {autenticado} = useAutenticacion();
    return (
        <Route {...rest} render={() => (autenticado ?children:<Redirect to="/signin"/>)}/>
        
    ) 
} 
